import add from "./components/add"
import add_list from "./components/add_list"
import add_info from "./components/add_info"

export default[
  {path:"/",component:add_list,name:"add_list"},
  {path:"/add",component:add,name:"add"},
  {path:"/add_info",component:add_info,name:"add_info"},
]


