<template>
  <div class="add">
    <div class="mauto">
      <!-- <h1>写博客</h1> -->
      <div v-if="!zhi">
        <label for>标题:</label>
        <input class="txt" type="text" v-model="bd.title">
        <label for>留言板：</label>
        <textarea v-model="bd.con"></textarea>
        <div class="li25 clearfix">
          <label data-v-8bc133be for>分类：</label>
          <div class="clearfix">
            <div class="fl mr10">
              <input v-model="bd.lists" type="checkbox" name id value="vue.js">vue.js
            </div>
            <div class="fl mr10">
              <input v-model="bd.lists" type="checkbox" name id value="React.js">React.js
            </div>
            <div class="fl mr10">
              <input v-model="bd.lists" type="checkbox" name id value="AngularJS">AngularJS.js
            </div>
          </div>
        </div>
        <div>
          <label data-v-8bc133be for>作者：</label>
          <select name id v-model="bd.zuozhe">
            <option v-for="guide in guides">{{guide}}</option>
          </select>
        </div>
        <button @click="tijiao">提交</button>
      </div>
      <div class="fb tac fb li30" v-if="zhi">恭喜您！添加成功！</div>
      <hr>
      <div class="li25">
        <div class="f16">
          <em class="fb">标题：</em>
          {{bd.title}}
        </div>
        <div class="f16">
          <em class="fb">留言内容：</em>
          {{bd.con}}
        </div>
        <div class="f16 txt2"></div>
        <div class="f16">
          <em class="fb">分类：</em>
          <ul class="clearfix">
            <li class="fl" v-for="list in bd.lists">{{list}}</li>
          </ul>
        </div>
        <div>作者：{{bd.zuozhe}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "hello",
  data() {
    return {
      bd: {
        title: "",
        con: "",
        lists: [],
        zuozhe: []
      },
      guides: ["hongsm", "张三", "里斯"],
      zhi: false
    };
  },
  methods: {
    tijiao: function() {
      this.$http
        .post("http://jsonplaceholder.typicode.com/posts", {
          title: this.bd.title,
          body: this.bd.con,
          userId: 666
        })
        .then(function(data) {
          // console.log(data);
          this.zhi = true;
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.mauto {
  max-width: 750px;
  padding: 0 5%;
  margin: 20px auto;
}
label {
  display: block;
  font-weight: bold;
  font-size: 16px;
  margin: 15px 0;
}
.txt {
  width: 100%;
  .h(45px);
  border: 1px solid #ccc;
  padding: 0 20px;
}
textarea {
  width: 100%;
  height: 200px;
  padding: 20px;
  border: 1px solid #ccc;
}
button {
  display: block;
  width: 50%;
  .h(45px);
  .hsmbg();
  color: #fff;
  font-size: 16px;
  margin: 20px auto;
}
.mr10 {
  margin-right: 20px;
}
</style>



