<template>
  <div v-bg="'#f8f8f8'" class="add-list">
    <div class="mauto">
      <input type="text" class="search" v-model="search" placeholder="搜索">
      <ul class="list">
        <li v-for="list in search_list">
          <h3>{{list.title | dadaxie}}</h3>
          <div v-color class="txt">{{list.body | hang}}</div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-list",
  data() {
    return {
      lists: [],
      search:"",
    };
  },
  created() {
    this.$http
      // .get("http://jsonplaceholder.typicode.com/posts")
      .get("../../static/posts.json")
      .then(function(data) {
        this.lists = data.body.slice(0, 20);
      });
  },
  computed: {
    search_list: function(){
      return this.lists.filter((list)=>{
        return list.title.match(this.search)
      })
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.mauto {
  max-width: 750px;
  padding: 0 5%;
}
.search {
  margin-top: 30px;
  width: 100%;
  .h(50px);
  padding: 0 20px;
  border-radius: 10px;
  color: #000;
  font-weight: bold;
  font-size: 16px;
  background: #eee;
}
.list {
  margin: 20px auto;
  li {
    padding: 20px;
    border-radius: 10px;
    background: #efefef;
    margin-bottom: 10px;
  }
  h3 {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 5px;
  }
  .txt {
    font-size: 16px;
    line-height: 25px;
  }
}
</style>



